import React from "react"
import { Layout, Section, Card, Heading } from "components"
import { Row, Col, Typography, Carousel, Divider } from "antd"
import TutorCard from "components/common/TutorCard"
import { HorizontalRow } from "components"
import DynamicIcon from "components/common/DynamicIcon"
import styled from "styled-components/macro"
import { mobile, media } from "helpers"
import { useTutors } from "helpers/hooks/data"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { fetchLifelongSliders } from "store/actions/contentfulActions"
import { Emoji } from "components"
import { Empty } from "components"
import { Button } from "components"
import { useHistory } from "react-router"
import { country } from "App"

const StyledCarousel = styled(Carousel)`
	.slick-track {
		max-height: 300px;
		overflow-y: hidden;
		img {
			height: 100%;
			object-fit: contain;
		}
	}
`

const SectionHeading = styled(Heading).attrs({
	level: 4,
	marginBottom: "0"
})``

export default function Lifelong() {
	const [searchTutors, { data: allTutors }] = useTutors()
	const dispatch = useDispatch()
	const { push } = useHistory()
	const sliders = useSelector(({ contentful }) => contentful.lifelongSliders)

	const photographyCoaches = allTutors.filter((item) => {
		const ids = item.nonAcademic.map((item) => item.pvid)
		return ids.includes(22)
	})

	const designCoaches = allTutors.filter((item) => {
		const ids = item.nonAcademic.map((item) => item.pvid)
		return ids.includes(20)
	})

	const musicCoaches = allTutors.filter((item) => {
		const ids = item.nonAcademic.map((item) => item.pvid)
		return ids.includes(24)
	})

	const handleClickCoach = (coach) => {
		push(`/${country}/${coach.token}--${coach.pvid}--${coach.firstName}--${coach.lastName}`)
	}

	useEffect(() => {
		dispatch(fetchLifelongSliders())
		searchTutors({ variables: { nonAcademicPvid: "all" } })
	}, [dispatch, searchTutors])

	return (
		<Layout>
			<Section centered maxWidth="1200px">
				<Heading
					content={
						<Typography>
							Cudy <span className="primary">Lifelong</span>
						</Typography>
					}
					subheader="Find experienced coaches to teach you everything"
					marginBottom="3em"
				/>

				<StyledCarousel slidesToShow={1} slidesToScroll={1} className="mb4em">
					{sliders.map((item) => (
						<img key={item.image} src={item.image} width="100%" />
					))}
				</StyledCarousel>

				<FeaturedCoaches coaches={allTutors} onClick={handleClickCoach} />

				<CategorizedSection
					coaches={musicCoaches}
					emoji={<Emoji icon="🎸" label="music" />}
					heading={{ title: "music", description: "They teach best at musics" }}
					onClick={handleClickCoach}
				/>

				<CategorizedSection
					coaches={designCoaches}
					emoji={<Emoji icon="🎨" label="art" />}
					heading={{ title: "design", description: "They teach best at design and art" }}
					onClick={handleClickCoach}
				/>

				<CategorizedSection
					coaches={photographyCoaches}
					emoji={<Emoji icon="📸" label="camera" />}
					heading={{ title: "photography", description: "They teach best at producing phenomenal images" }}
					onClick={handleClickCoach}
				/>

				<Divider />

				<Row type="flex" justify="center" className="mb4em">
					<Col lg={12}>
						<Button block type="dashed" onClick={() => push(`/${country}/lifelong/browse`)}>
							<DynamicIcon type="icon-male" />
							&nbsp; Find other categories our coaches teaching
						</Button>
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}

/**
 * 🐣🐣🐣🐣🐣🐣 // =======================================================================================================
 * @name CATEGORIZED_SECTION
 * @memberof LIFELONG
 */

function CategorizedSection({ coaches = [], heading = {}, emoji, onClick }) {
	const { title, description } = heading

	return (
		<Section noPadding>
			<SectionHeading
				content={
					<Typography>
						Best in <span className="primary">{title}</span>&nbsp; {emoji}
					</Typography>
				}
				subheader={description}
			/>
			<Row gutter={32} type="flex" className="mb2em">
				<Col lg={24} xs={24}>
					{coaches.length === 0 ? (
						<Empty description={`No ${title} coaches found`} />
					) : (
						<HorizontalRow height="100%" padding="2em 0">
							{coaches.map((person) => (
								<Col lg={5} xs={16} key={person.id}>
									<TutorCard {...person} onClick={() => onClick(person)} boxShadow="none" />
								</Col>
							))}
						</HorizontalRow>
					)}
				</Col>
			</Row>
		</Section>
	)
}

/**
 * 🐣🐣🐣🐣🐣🐣 // =======================================================================================================
 * @name FEATURED_CARD
 * @memberof LIFELONG
 */

const FeaturedCard = styled(Card).attrs({
	height: "auto",
	bg: "aliceblue"
})`
	&& {
		margin-bottom: 0;
	}

	${media.mobile`
		.ant-card-body {
			padding: 0 24px;
		}
	`}
`

function FeaturedCoaches({ coaches = [], onClick }) {
	return (
		<Row gutter={32} type="flex" className="mb4em">
			<Col lg={6} xs={24} className={mobile && "mb2em"}>
				<FeaturedCard padding={mobile ? "1em" : "2em"}>
					<DynamicIcon type="icon-CloudSunWeatherTe" size={46} />
					<SectionHeading
						content="Featured coaches"
						subheader="Here's a few of our best coaches among many others who have an expertise at what they do"
					/>
					{mobile && (
						<HorizontalRow height="100%" padding="2em 0">
							{coaches.map((person) => (
								<Col lg={7} xs={16} key={person.pvid}>
									<TutorCard {...person} onClick={() => onClick(person)} className="mb0" boxShadow="none" />
								</Col>
							))}
						</HorizontalRow>
					)}
				</FeaturedCard>
			</Col>
			{!mobile && (
				<Col lg={18} xs={24}>
					<HorizontalRow height="350px">
						{coaches.map((person) => (
							<Col lg={7} xs={10} key={person.pvid}>
								<TutorCard {...person} onClick={() => onClick(person)} className="mb0" boxShadow="none" />
							</Col>
						))}
					</HorizontalRow>
				</Col>
			)}
		</Row>
	)
}
